<template>
  <div id="logManagement">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: calc(100% - 80px)" ref="getQuery" id="logQuery"></QueryBox>
      <div class="queryBtns">
        <Button type="primary" size="small" icon="md-cloud-download" @click="handleExport" class="exportBtn">导出</Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row size="small" :max-height="tableHeight">
        </Table>
        <div class="page_box" id="pageBox">
          <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" size="small" @on-change="pageChange" show-total show-elevator/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import { getLogList, exportLogExcel } from '@/api/systemManage'
export default {
  name: 'logManagement',
  data () {
    return {
      /* 表格高度 */
      tableHeight: window.innerHeight - 290,
      /* 查询框查询条件 */
      queryData: {},
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '操作名称',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'title'
        },
        {
          title: '访问IP',
          align: 'center',
          // ellipsis: true,
          // tooltip: true,
          minWidth: 200,
          key: 'remoteAddr'
        },
        {
          title: '消息',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl010'
        },
        {
          title: '源',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl009'
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((window.innerHeight - 290) / 40) - 1,
      /* 加载 */
      loading: false
    }
  },
  components: {
    QueryBox
  },
  computed: {
    // 查询表单参数
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'title',
          title: '操作名称'
        }
      ]
    }
  },
  created () {
    this.getTableList()
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.tableHeight = window.innerHeight - 290
      this.pageSize = Math.floor((window.innerHeight - 290) / 40) - 1
      this.getTableList()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      getLogList({
        data: {
          pageSize: this.pageSize,
          pageNumber: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        } else {
          this.$Notice.error({ title: '获取日志列表失败', desc: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '获取日志列表失败' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* 导出文件 */
    handleExport () {
      exportLogExcel({}).then((res) => {
        if (res.data.code === '0000') {
          let data = res.data.data
          if (!data) {
            return
          }
          let link = document.createElement('a')
          link.style.display = 'none'
          let url = res.data.data.filepath
          link.href = '/epr' + url
          link.setAttribute('download', url.split('/')[url.split('/').length - 1])
          document.body.appendChild(link)
          link.click()
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    }
  }
}
</script>

<style scoped lang="scss">
  #logManagement {
    position: relative;
    .iconfont {
      margin-right: 4px;
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      margin-right: 10px;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
  }
</style>
